.icons {
  display: flex;
  align-items: center;
  font-size: 24px;
  cursor: pointer;
}

.delete-icon {
  margin-right: 5px;
  color: rgb(241, 134, 100);
}

.edit-icon {
  margin-right: 5px;
  color: rgb(247, 237, 100);
}

.check-icon {
  color: rgb(125, 247, 100);
}
